.i{
    display: flex;
    height: 100vh;
    margin-top: 2%;
}

.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-right{
    flex: 1;
    position: relative;
}
.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move{
    25%{
        transform: translateY(-50px);
    }

    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}
.i-title-item{
    height: 50px;
    font-size: 30px;
    color: #59b256;
    font-weight: bold;
    display: flex;
    align-items: center;
}
.i-img{
   
    width: 68%;
    height: 80%;
    object-fit: cover;
    position: absolute;
    margin: 19% 12%;
    
    
}
.i-bg{
    clip-path: polygon(50% 0%, 100% 0, 100% 100%, 18% 100%, 0% 38%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    right: 0;
}


@media  screen and (max-width:600px) {
    .i{
        flex-direction: column;
        
    }
    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
       
    }
    .i-img{
        height: 100%;
        width: 90%;
        margin: 5% 12%;
    }
   
   
    
}